* {
  margin: 0;
  padding: 0; }

nav {
  position: relative;
  font-family: "webfont";
  font-size: 7vw;
  margin-top: 15vh;
  margin-left: 20px;
  z-index: 5; }

.Link {
  text-decoration: none;
  color: white;
  position: absolute;
  transition: 0.5s; }

.Link:hover {
  transform: translateX(90px);
  transition: 0.5s; }

.iconNav {
  display: unset;
  position: absolute;
  top: 45px;
  left: -90px;
  width: 70px;
  height: 70px;
  color: transparent; }

.about {
  background-image: url(/src/images/navicons/aboutme.svg);
  background-repeat: no-repeat;
  background-size: contain; }

.proyect {
  background-image: url(/src/images/navicons/proyects.svg);
  background-repeat: no-repeat;
  background-size: contain; }

.contact {
  background-image: url(/src/images/navicons/contact.svg);
  background-repeat: no-repeat;
  background-size: contain; }

.socialicons {
  display: flex; }

.social {
  transition: 0.5s;
  width: 50px;
  height: 50px;
  margin-right: 10px;
  background-color: white;
  border-radius: 25%;
  border: white solid 1px;
  margin-top: 30px;
  cursor: pointer; }

.social:hover {
  width: 80px;
  height: 80px;
  transition: 0.5s; }

.id {
  background-image: url(/src/images/socialicons/id.svg);
  background-repeat: no-repeat;
  background-size: contain; }

.ig {
  background-image: url(/src/images/socialicons/ig.svg);
  background-repeat: no-repeat;
  background-size: contain; }

.fb {
  background-image: url(/src/images/socialicons/fb.svg);
  background-repeat: no-repeat;
  background-size: contain; }

.tw {
  background-image: url(/src/images/socialicons/tw.svg);
  background-repeat: no-repeat;
  background-size: contain; }

.ws {
  background-image: url(/src/images/socialicons/ws.svg);
  background-repeat: no-repeat;
  background-size: contain; }

li {
  user-select: none; }

@media screen and (max-width: 1440px) {
  nav {
    font-size: 5vw;
    margin-top: 25vh; }
  .iconNav {
    top: 23px;
    left: -70px;
    width: 50px;
    height: 50px; }
  .social {
    width: 30px;
    height: 30px;
    margin-top: 30px; } }

@media screen and (max-width: 1024px) {
  nav {
    margin-top: 15vh; }
  .iconNav {
    top: 13px;
    left: -70px;
    width: 40px;
    height: 40px; } }

@media screen and (max-width: 768px) {
  .lel {
    display: none; }
  .iconNav {
    left: -10px;
    width: 40px;
    height: 40px; }
  .Link:hover {
    transform: translateX(20px); }
  nav {
    margin-top: 1vh; }
  .socialicons {
    display: unset; }
  .social {
    width: 20px;
    height: 20px;
    margin-top: 30px;
